button {
    background-color: #454545; /* Green */
    border: none;
    color: rgb(231, 231, 231);
    padding: 7px 16px;
    margin: 5px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border-radius: 5px;
    font-weight: 500;
    font-size: 1rem;

    cursor: pointer;
}
